* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

body {
  background: #ececec;
  font-family: 'OpenSans-Regular';
}

img {
  width: 100%;
}

.wrap {
  max-width: 960px;
  margin: auto;
  padding: 0 15px;
}

.header .wrap .nav {
  margin-top: 40px;
  margin-bottom: 20px;
  display: flex;
  background: #fff;
}

.header .wrap .nav .menu {
  display: flex;
}

.header .wrap .nav .menu .home {
  height: 40px;
}

.header .wrap .nav .menu li {
  margin: auto 25px;
}

.header .wrap .nav .menu li:hover {
  color: #56ccc8;
  transition: .3s;
}

.header .wrap .nav .widgets {
  display: flex;
}

.header .wrap .nav .widgets .searching .form .search {
  width: 65%;
  margin: 10px;
  border: none;
}

.header .wrap .nav .widgets .searching .form button {
  display: inline-block;
  background: none;
  border: none;
}

.header .wrap .nav .widgets .searching .form button i {
  font-size: 18px;
  color: #ccc;
}

.header .wrap .nav .widgets .social {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 120px;
  padding: 0 10px;
  border-left: 2px solid #ececec;
  border-right: 2px solid #ececec;
}

.header .wrap .nav .widgets .login {
  margin: auto 20px;
}

.header .wrap .interesting .slider .item {
  background: url('../img/slider.png');
  height: 350px;
  color: #fff;
  display: flex;
  flex-direction: column;
  padding-top: 50px;
  padding-left: 50px;
}

.header .wrap .interesting .slider .item .title {
  display: inline-block;
  font-size: 46px;
}

.header .wrap .interesting .slider .item .text {
  display: inline-block;
  font-size: 14px;
  max-width: 400px;
  margin-top: 30px;
}

.section {
  margin-top: 20px;
}

.section .wrap {
  display: grid;
  grid-template-columns: 4fr 8fr;
}

.section .wrap .aside {
  margin-right: 20px;
}

.section .wrap .aside .menu .item {
  padding-top: 20px;
  padding-bottom: 20px;
  background: #1f3045;
}

.section .wrap .aside .menu .item h2 {
  padding: 0 35px;
  font-size: 16px;
  font-weight: 100;
  color: #56ccc8;
}

.section .wrap .aside .menu .item ul {
  padding-left: 35px;
}

.section .wrap .aside .menu .item ul li {
  padding-top: 15px;
  font-size: 15px;
  color: #fff;
}

.section .wrap .aside .menu .item:nth-child(2) {
  background: #324864;
}

.section .wrap .aside .news {
  margin-top: 20px;
  background: #fff;
}

.section .wrap .aside .news .title {
  font-size: 24px;
  font-weight: 100;
  padding: 15px;
}

.section .wrap .aside .news .items .item {
  display: flex;
  padding: 0 15px 20px;
}

.section .wrap .aside .news .items .item img {
  max-width: 71px;
}

.section .wrap .aside .news .items .item .desc {
  margin-left: 15px;
}

.section .wrap .aside .news .items .item .desc p {
  font-size: 15px;
}

.section .wrap .aside .news .items .item .desc span {
  font-size: 14px;
  color: #aaa;
}

.section .wrap .aside .news .items .item:nth-child(1) {
  padding-top: 20px;
  border-top: 2px solid #ececec;
}

.section .wrap .aside .request {
  margin-top: 20px;
  background: #fff;
  min-height: 341px;
}

.section .wrap .aside .request .title {
  font-size: 24px;
  font-weight: 100;
  padding: 15px 20px;
}

.section .wrap .aside .request #myForm {
  margin-left: 20px;
  margin-bottom: 10px;
}

.section .wrap .aside .request #myForm input {
  height: 35px;
  width: 90%;
  margin-bottom: 15px;
  font-size: 14px;
  color: #aaa;
  border: 1px solid #ccc;
  border-radius: 2px;
  padding: 10px;
}

.section .wrap .aside .request #myForm .question {
  height: 100px;
}

.section .wrap .aside .request button {
  margin-left: 20px;
  padding: 5px 20px;
  background: none;
  border: 1px solid #ccc;
  border-radius: 3px;
  color: #aaa;
}

.section .wrap .main .items .item {
  margin-bottom: 20px;
  height: 450px;
  background: #fff;
}

.section .wrap .main .items .item .title {
  margin-top: 30px;
  display: flex;
  font-size: 14px;
}

.section .wrap .main .items .item .title img {
  display: inline-block;
  width: 40px;
  margin-right: 35px;
  margin-bottom: 20px;
}

.section .wrap .main .items .item .title h2 {
  display: inline-block;
  font-weight: 100;
}

.section .wrap .main .items .item .desc {
  width: 470px;
  margin: auto;
  margin-bottom: 50px;
  font-size: 15px;
}

.section .wrap .main .items .item .desc span {
  color: red;
}

.section .wrap .main .items .item .stats {
  display: flex;
  justify-content: space-around;
  padding: 0 40px;
}

.section .wrap .main .items .item .stats p {
  font-size: 15px;
}

.section .wrap .main .items .item .stats p img {
  width: 20px;
  margin-right: 5px;
}

.footer .wrap .bottom {
  height: 45px;
  background: #333333;
  display: flex;
  justify-content: space-between;
}

.footer .wrap .bottom p {
  font-size: 14px;
  color: #fff;
  margin: auto 15px;
}

.footer .wrap .bottom p span {
  color: #56ccc8;
}

.footer .wrap .bottom .menu {
  display: flex;
  align-items: center;
  font-size: 14px;
  margin-right: 15px;
}

.footer .wrap .bottom .menu li {
  color: #fff;
  margin: 5px;
}

.footer .wrap .bottom .menu li:nth-child(3) {
  color: #56ccc8;
}

.button {
  font-size: 14px;
  text-transform: uppercase;
  padding: 11px 58px;
  background: none;
  border: 1.5px solid #fff;
  border-radius: 2px;
  color: #fff;
  width: 160px;
  height: 46px;
  margin-top: 30px;
}

.button:hover {
  color: #fff;
  text-decoration: none;
}

.button_red {
  font-size: 14px;
  padding: 8px 38px;
  background: #ea6060;
  border: 1.5px solid #fff;
  border-radius: 5px;
  color: #fff;
  margin-left: 20px;
}